import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder } from '../../../../node_modules/@angular/forms';
import { MainService } from '../../providers/mainService';
import {IMyDpOptions, IMyDateModel} from 'mydatepicker';
import { setRootDomAdapter } from '../../../../node_modules/@angular/platform-browser/src/dom/dom_adapter';
declare var  $:any

@Component({
  selector: 'app-transaction-management',
  templateUrl: './transaction-management.component.html',
  styleUrls: ['./transaction-management.component.css']
})
export class TransactionManagementComponent implements OnInit {

  transantionForm: FormGroup;
  paginationData: any={};
  srNo: number;
  modified=[]
  Transaction:any=[];
  // eventData:any=[];
  pageSize:any= {};
  pageNo: any = 1;
  status:any
beDisable=true
todate:any;


  private fromDate: IMyDpOptions = { 
    dateFormat: 'yyyy-mm-dd', 
    editableDateField:false, 
    openSelectorOnInputClick:false,
    disableUntil: {year: 0, month: 0, day: 0}
    };
   
    public toDate: IMyDpOptions = { 
      dateFormat: 'yyyy-mm-dd', 
      editableDateField:false, 
      openSelectorOnInputClick:false,
      disableUntil: {year: 0, month: 0, day: 0}
      };

  constructor(public fb: FormBuilder, public service : MainService) {
    this.transantionForm = fb.group({
      'search':[''],
      'from':[null],
      'to':[null],
      'sort': [''],
    })
   }

  
   ngOnInit() {
    this.getAllTransaction()
  }

    //****************************GET ALL transaction *************************************** */
    changePage(page){
      console.log('page no--->',page)
    this.pageNo= page
    this.getAllTransaction()
    // this.search()
      }
    
      filter(num:number) {
        this.pageNo=1
        // this.getAllTransaction()
        setTimeout(() => {         
          this.getAllTransaction()
        }, 300)
        // if(num==1){
        //   console.log('filter search')

        // }
        // else if(num==2){
        //   console.log('filter by drop down',)
        // } 
        // else if(num==3){
        //   console.log('filter by date',$('#todate').val())
        //   console.log('filter by date',this.todate)
         
        // }
      }
      getAllTransaction() { 
        if(!(this.transantionForm.value.from && this.transantionForm.value.to)) {
          this.transantionForm.patchValue({
            'to': null,
            'from': null
          })
        } 
    let postData = {
            search: this.transantionForm.controls['search'].value,
            bookingStatus: this.transantionForm.controls['sort'].value,
            fromDate : this.transantionForm.value.from ? this.transantionForm.value.from.formatted : '',
            toDate : this.transantionForm.value.to ? this.transantionForm.value.to.formatted: '',
            pageNumber: this.pageNo          
          }
        console.log("BEFORE POSTING DATA===>",postData)
          // for (let val in postData) {
        
          //   if (postData[val] == '') {
          //     delete postData[val]
          //   }
          // }  
    this.service.postApi(`event/transactionManagementFilter`,postData,1).subscribe(response => {
      // console.log('res')
      if (response['response_code'] == 200) {
        // console.log(' Event data found successfully', response)
        this.modified = response[`result`]
        this.Transaction=this.modified
        for(let i=0; i<this.modified.length; i++){
          if(this.modified[i].bookingStatus == 'CANCELLED'){
            this.paginationData = response[`paginationData`]
            this.srNo = (this.pageNo - 1) * this.paginationData.limit
                     }
          else if(this.modified[i].bookingStatus == 'CONFIRMED'){
            this.paginationData = response[`paginationData`]
            this.srNo = (this.pageNo - 1) * this.paginationData.limit
          }
          
        }


        this.paginationData = response[`paginationData`]
        this.srNo = (this.pageNo - 1) * this.paginationData.limit
       
        console.log("############",this.paginationData)
        
      }
      // else{
      //   this.service.toastErr(response['response_message'])
      // }
      else {
             this.Transaction = [];
                console.log('Failure', response['responseMessage'])
              }
    }, error => {
      console.log('error =>', error)
      this.service.toastErr('Something Went Wrong')
    })
  }




  // filterChange(){
  //   console.log('FILTER FORM VALUES-->', this.transantionForm.value)
  // }
  onFromDate(event : IMyDateModel){
    console.log(this.transantionForm.value)
    // console.log(event)
    if(event.formatted) {
        this.beDisable = false
      let d: Date = new Date(event.jsdate.getTime());
      d.setDate(d.getDate() -  1);
      let copy: IMyDpOptions = this.getCopyOfToDateOpt();
      copy.disableUntil = {year: d.getFullYear(),
        month: d.getMonth() + 1,
        day: d.getDate()
      };
      this.toDate = copy;
    } else {
      this.beDisable = true
      // this.filter(3)

      this.transantionForm.patchValue({
        'to': null,
        'from': null
      })
      this.getAllTransaction()
    }
    
  }
  getCopyOfToDateOpt(): IMyDpOptions {
    return JSON.parse(JSON.stringify(this.toDate));
}
onToDate(event){
  console.log('TO DATE EVENT IS--->', event)
  
  setTimeout(() => {
    console.log(this.transantionForm.controls['to'].value)
    console.log(this.transantionForm.controls['from'].value)
    if(this.transantionForm.controls['to'].value.formatted && this.transantionForm.controls['from'].value.formatted) {
      this.filter(3)
    }
    else{
      this.transantionForm.patchValue({
        'to': null
      })
    }
   
  }, 300)
  
  
  // console.log('Form Value is+++++++++++', this.transantionForm.value)
}


}
