import { Component, OnInit } from '@angular/core';
import { MainService } from '../../providers/mainService';
import { NgModule } from '@angular/core';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';
import { CookieService } from 'ngx-cookie-service';
declare var $: any


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  user: any;
  loginForm: FormGroup;
  remberMe: any = []
  // remberMeEmail:any
  // remberMePass:any
  // remberMeCheck:any
  token: any;
  responseData:any ={};

  constructor(private router: Router, private formBuilder: FormBuilder, public service: MainService, public cookieService: CookieService) { }
  ngOnInit() {
    this.loginForm = this.formBuilder.group({
      'email': ['', Validators.compose([Validators.required, Validators.pattern(/^[A-Z0-9_]+([\.][A-Z0-9_]+)*@[A-Z0-9-]+(\.[a-zA-Z]{2,3})+$/i), Validators.maxLength(50)])],
      //'email': ['', [Validators.required, Validators.pattern("[^ @]*@[^ @]*")]],
      //'pass':['', [Validators.required, Validators.minLength(8)]],
      'pass': ['', Validators.compose([Validators.required, Validators.maxLength(16), Validators.minLength(8)])],
      'remberMe': [false]
    });
    $(function () {
      $('#email,#pass').on('keypress', function (e) {
        if (e.which == 32)
          return true;
      });
    });
    // if(localStorage.getItem('LoginData') != null) {
    //   let loginData = JSON.parse(localStorage.getItem('LoginData'))
    //   this.loginForm.controls['email'].setValue(loginData.email)
    //    this.loginForm.controls['pass'].setValue(loginData.pass)
    //   this.loginForm.controls['remberMe'].setValue(loginData.remberMe)
    // }
    this.loginForm.patchValue({
      email: this.cookieService.get('email'),
      pass: this.cookieService.get('password'),
      remberMe: this.cookieService.check('remberMe')
    })
    console.log("i am hereeeeee>>>>",this.cookieService.get('email'),this.cookieService.get('password'),this.cookieService.check('remberMe'))
  }
  login(dataa) {
    // if(this.loginForm.dirty && this.loginForm.valid)
    // alert(`Email: ${this.loginForm.value.email} Password: ${this.loginForm.value.pass} `)
    // console.log(this.loginForm.value)
    console.log(dataa)
    let loginData = {
      "email": dataa.email,
      "password": dataa.pass,
      "userType": "SUPERADMIN"
    }
    console.log(loginData)
    this.service.postApi('user/login', loginData, 0).subscribe(response => {
      this.responseData = response;
      // console.log("Pramod"+JSON.stringify(this.responseData))
      // console.log(`Login Data====>${JSON.stringify(response)}`);
      if (response['response_code'] == 200) {
        this.service.showSuccess(response['response_message'])
        // console.log("success")
        localStorage.setItem('token',this.responseData.token)
        localStorage.setItem('adminId',this.responseData.result._id)  
        this.router.navigate(['/dashboard'])      
        localStorage.token = response[`token`];
        localStorage.adminId = response[`result`][`_id`];
        console.log('successfully login', response['response_message'])

        if (this.loginForm.controls['remberMe'].value == true) {
          console.log('remberMe me is true ')
          console.log("i am hereeeeee>>>>",this.cookieService.get('email'),this.cookieService.get('password'),this.cookieService.check('remberMe'))

          this.cookieService.set('email', this.loginForm.controls['email'].value)
          this.cookieService.set('password', this.loginForm.controls['pass'].value)
          this.cookieService.set('remberMe', this.loginForm.controls['remberMe'].value)

        }
        else {
          this.cookieService.delete('email')
          this.cookieService.delete('password')
          this.cookieService.delete('remberMe')
          console.log('remberMe  is false ')
          console.log(`in else ${response['response_message']}`)

        }
      }
      else {
        this.service.toastErr(response['response_message'])
      }
    },
      error => {
        console.log('error occur', error)
        this.service.toastErr(`Something went wrong`)
     
      })

  }
}

